*{
    font-family: 'Roboto Slab', serif;
}

.welcome {
    font-size: 3em;
}
.home-container{
    margin: 10vh 0px ;
    font-size: 1em;
    color: white;
    font-family: 'Roboto Slab', serif;
}
#room-form{
    margin: 20px 30%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#room-form input {
    font-size: 2em;    
    width:200px;
    padding:20px;
    box-shadow:0 0 15px 4px rgba(0,0,0,0.06);
    border-radius: 11px;
    outline: none;
    margin: 0px 15px ;
    color: white;
    border: none;
    background-color: #ffffff1c;
  
}
  
.error{
    display: flex;
    height: 3rem;
    justify-content: center;
    align-items: center;
    padding: 0px 20px;
    width: fit-content;
    margin: auto;
    margin-top: 20px;
    border-radius: 13px;
    background-color: #ff000042;
    color: rgb(252, 207, 207);
}
